@import "reset";
@import "vars";
@import "mixins";
.wrapper {
    overflow: hidden;
    font-family: $font-primary;
    @include rfs(14); // Responsive Font-Size
    color: #514e4e;
}

.container {
    width: 98%; //custom padding    
    max-width: 1080px;
    margin: 0 auto;
}

button {
    background-color: transparent;
    border: none;
}

a {
    color: inherit;
    font-size: inherit;
    transition: all 0.2s ease;
}

// HEADER
.header-wrap {
    padding-bottom: 5px;
    padding-top: 15px;
}

.top-navbar-wrap {
    background: url("../img/menu_bg.jpg") repeat scroll center 0 transparent;
}

.top-navbar {
    min-height: 36px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #514e4e;
}

.navbar {
    display: flex;
    align-items: center;
    &__item {
        margin-right: 7%;
        flex-shrink: 0;
        color: inherit;
        &:hover {
            color: #8d46c2;
        }
    }
}

.work-time {}

.top-content {
    margin: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo-box {
    position: relative;
    max-width: 250px;
    &:after {
        content: "Севастополь";
        position: absolute;
        top: 0;
        right: 0;
        font-size: 12px;
    }
    img {
        width: 100%;
    }
}

.order-btn,
.price-btn {
    display: flex;
    align-items: center;
    padding-right: 1%;
    color: #514e4e;
    text-transform: uppercase;
    box-sizing: border-box;
    border-radius: 10px;
    border: 2px solid transparent;
    cursor: pointer;
    transition: all 0.3s ease;
    &:hover {
        border: 2px solid #8d46c2;
        i {
            border-color: transparent;
        }
    }
    i {
        width: 64px;
        height: 64px;
        border-radius: 50%;
    }
    span {
        padding-left: 3%;
        line-height: 1.5;
        text-align: left;
        flex-shrink: 0;
        font-weight: 700;
    }
}

.order-btn {
    i {
        background: url("../img/icon--keyboard.png") center no-repeat;
        border: 2px solid #8d46c2;
    }
}

.price-btn {
    i {
        background: url("../img/icon--price.png") center no-repeat;
        border: 2px solid #76bf16;
    }
    &:hover {
        border: 2px solid #76bf16;
    }
}

.phones {
    line-height: 2;
}

.phones__number {
    padding: 0px 0 2px 25px;
    @include rfs(18);
    font-weight: 700;
    background: url("../img/icon--phone.png") no-repeat;
    background-size: 20px 20px;
    &:hover {
        color: #8d46c2;
    }
}

.phones__number--mts {
    background: url("../img/icon--mts.png") no-repeat;
    background-size: 20px 20px;
}

.phone-btn {
    padding: 7px 20px;
    color: #fff;
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
    background-color: #8d46c2;
    &:hover {
        opacity: 0.8;
    }
}

// HERO SLIDER
.hero-slider__item {
    position: relative;
    .container {
        position: absolute;
        top: 10%;
        left: 0;
        right: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .body_wrap .title {
        @include rfs(48);
        line-height: 1.2;
        text-transform: uppercase;
        color: #8d46c2;
        font-weight: 700;
        position: relative;
        padding-bottom: 20px;
        margin-bottom: 10px;
        &::after {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            bottom: 0;
            width: 110px;
            height: 5px;
            background: #8d46c2;
        }
    }
    .body {
        @include rfs(36);
        line-height: 1.3;
        text-transform: uppercase;
        font-weight: 700;
    }
}


/*form*/

.form_2 {
    max-width: 300px;
    padding: 15px 30px;
    text-align: center;
    background-color: #ffffff;
    box-sizing: border-box;
}

.form_2 .title {
    @include rfs(20);
    padding: 15px 0;
    text-transform: uppercase;
    font-weight: 700;
    line-height: 1.3;
}

.form_2 input {
    margin-bottom: 15px;
}

.form-control {
    display: block;
    width: 100%;
    height: 34px;
    padding: 6px 12px;
    box-sizing: border-box;
    @include rfs(14);
    line-height: 1.42857143;
    color: #555555;
    background-color: #ffffff;
    background-image: none;
    border: 1px solid #cccccc;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -webkit-transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
    -o-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
}

.form_submit {
    padding-right: 20px;
    padding-left: 20px;
    @include rfs(16);
    text-transform: uppercase;
    font-weight: 700;
    color: #fff;
    padding: 6.5px 25px;
    background: #ee008c;
    border: none;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}

.form_submit:hover {
    opacity: 0.8;
}

// common styles
// subtitle
.subtitle {
    position: relative;
    padding: 30px 0 15px 0;
    margin-bottom: 30px;
    @include rfs(24);
    text-transform: uppercase;
    font-weight: 700;
    &:after {
        content: '';
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        background: #76bf16;
        width: 110px;
        height: 2px;
    }
}

.body__text {
    @include rfs(16);
    line-height: 1.3;
}

// COMPANY
.company {
    .title {
        margin: 30px 0;
        line-height: 1.3;
        text-align: center;
        @include rfs(36);
        color: #8d46c2;
        text-transform: uppercase;
        font-weight: 700;
        padding-bottom: 10px;
    }
    &__content {
        padding-bottom: 10px;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #cccccc;
        span {
            font-weight: 700;
        }
    }
    &__text {
        max-width: 40%;
    }
    &__authorized {
        margin: 30px 0;
    }
    // how we work
    &__work-items {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 30px;
    }
    &__work-item {
        position: relative;
        padding-right: 7%;
        padding-left: 5%;
        &:after {
            content: '';
            display: block;
            position: absolute;
            right: 17px;
            top: 50%;
            margin-top: -32px;
            width: 22px;
            background: url("../img/work--next_arrow.png") no-repeat scroll 0 0 transparent;
            height: 60px;
        }
    }
    .company__work-item--1 {
        background: url("../img/work--pencil.png") no-repeat scroll 0 0 transparent;
    }
    .company__work-item--2 {
        background: url("../img/work--house.png") no-repeat scroll 0 0 transparent;
    }
    .company__work-item--3 {
        background: url("../img/work--window.png") no-repeat scroll 0 0 transparent;
    }
    .company__work-item--4 {
        background: url("../img/work--key.png") no-repeat scroll 0 0 transparent;
    }
    &__work-caption {
        @include rfs(18);
        font-weight: 700;
        line-height: 1.3;
    }
}

// BLOCK CHOICE 
.choice {
    background-color: #edf2f6;
    padding-bottom: 15px;
    .choice__row {
        display: flex;
        margin-bottom: 30px;
        img {
            max-height: 85px;
        }
        .body__text {
            font-weight: 300;
            line-height: 1.5;
            ul {
                display: flex;
                flex-wrap: wrap;
                li {
                    width: 50%;
                }
            }
        }
    }
    .choice__icon {
        margin-right: 2%;
    }
    .choice__title {
        @include rfs(22);
        font-weight: 700;
        line-height: 4;
        color: #8d46c2;
    }
    .choice__order {
        text-align: center;
        .phone-btn {
            display: inline-block;
            height: 40px;
            line-height: 40px;
            background-color: #76bf16;
        }
    }
}

// BENEFITS
.benefits {
    .benefits__items {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
    }
    .benefits__item {
        width: 30%;
        text-align: center;
        margin-bottom: 60px;
        @include rfs(20);
        span {
            font-weight: 600;
        }
    }
    .benefits__caption {
        margin-top: 15px;
        line-height: 1.3;
    }
    .benefits__order {
        padding: 15px 30px;
        display: inline-block;
        border: 2px solid #76bf16;
        color: #76bf16;
        border-radius: 5px;
        transition: all 0.3 ease;
        &:hover {
            background-color: #76bf16;
            color: #fff;
        }
    }
}